
*{
    margin: 0;
}
#app{
    min-height: 100vh;
    background: radial-gradient(#edeced, #e9e9ea);
}
.cursors {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap
}
.cursors > div{
    -webkit-box-flex: 200px;
        -ms-flex: 200px;
            flex: 200px;
    padding: 10px 0;
    white-space: nowrap;
    margin: 4px;
    text-align: center;
    background: #fff;
    border-radius: 2px;
}
.cursors > div:hover{
    -webkit-box-shadow: 0 4px 10px rgba(186, 188, 210, .3);
            box-shadow: 0 4px 10px rgba(186, 188, 210, .3);
}
